import React, { useState } from "react";
import {
  Grid,
  Paper,
  Typography,
  Checkbox,
  FormControlLabel,
  FormGroup,
  Divider,
  Input
} from "@material-ui/core";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import Slider from "@material-ui/core/Slider";
import { withStyles, makeStyles } from "@material-ui/core/styles";

const useStyle = makeStyles(theme => ({
  checkBoxLabel: {
    fontSize: "1em",
    fontFamily: "Roboto",
    paddingLeft: "0.4em"
  },
  checkBoxIconWrapper: {
    padding: "0.2em 0.2em"
  },
  checkBoxIcon: {
    width: "0.8em",
    height: "0.8em"
  },
  filterTitle: {
    fontWeight: "600",
    textTransform: "uppercase",
    paddingBottom: "0em"
  },
  input1: {
    height: "10px"
  }
}));

function renderCheckBoxFilter(classes, items) {
  return (
    <FormGroup row={false} style={{ paddingLeft: ".4em" }}>
      {items.map(eachItem => (
        <FormControlLabel
          key={eachItem}
          control={
            <Checkbox
              icon={
                <CheckBoxOutlineBlankIcon
                  className={classes.checkBoxIcon}
                  fontSize="small"
                />
              }
              checkedIcon={
                <CheckBoxIcon
                  id="icon"
                  className={classes.checkBoxIcon}
                  fontSize="small"
                />
              }
              name="checkedI"
              color="primary"
              classes={{ root: classes.checkBoxIconWrapper }}
            />
          }
          label={eachItem}
          classes={{ label: classes.checkBoxLabel }}
        />
      ))}
    </FormGroup>
  );
}

const PrettoSlider = withStyles({
  root: {
    color: "#52af77",
    height: 8
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: "#fff",
    border: "2px solid currentColor",
    marginTop: -8,
    marginLeft: -12,
    "&:focus, &:hover, &$active": {
      boxShadow: "inherit"
    }
  },
  active: {},
  valueLabel: {
    left: "calc(-50% + 4px)"
  },
  track: {
    height: 8,
    borderRadius: 4
  },
  rail: {
    height: 8,
    borderRadius: 4
  }
})(Slider);

export default function FilterSidebar(props) {
  const classes = useStyle();

  const immutableFilterList = props.filters.filter;

  function changeText(setBrandFilterList, value) {
    if (value.trim() === "") setBrandFilterList(immutableFilterList);
    let brandValue = [];
    for (let index = 0; index < immutableFilterList.length; index++) {
      if (
        immutableFilterList[index].toLowerCase().startsWith(value.toLowerCase())
      )
        brandValue.push(immutableFilterList[index]);
    }
    setBrandFilterList(brandValue);
  }

  const [brandFilterList, setBrandFilterList] = useState(props.filters.filter);
  return (
    <Paper
      style={{
        display: "flex",
        alignItems: "center",
        borderRadius: "0"
      }}>
      {/* Container For All Filter */}
      <Grid id="all-filter-container" container direction="column">
        {/* Each Filter Container */}

        <Grid
          id="each-filter-container"
          container
          item
          direction="column"
          style={{ padding: "1.5em" }}>
          <Grid item>
            <Typography variant="subtitle1" className={classes.filterTitle}>
              {props.filters.title}
            </Typography>
          </Grid>

          {props.filters.type === "COUNT" ? (
            <React.Fragment>
              <Grid item>
                <Input
                  onChange={event => {
                    changeText(setBrandFilterList, event.target.value);
                  }}
                  placeholder="Search Brand"
                />
              </Grid>
              <Grid item>
                <PrettoSlider
                  valueLabelDisplay="auto"
                  aria-label="pretto slider"
                  defaultValue={[0, 20]}
                />
              </Grid>
            </React.Fragment>
          ) : (
            undefined
          )}

          <Grid item>
            {renderCheckBoxFilter(classes, brandFilterList, setBrandFilterList)}
          </Grid>
        </Grid>
        <Grid item>
          <Divider
            orientation="horizontal"
            variant="fullWidth"
            flexItem
            style={{ margin: "0 15px", height: "1px" }}
          />
        </Grid>
      </Grid>
    </Paper>
  );
}
