import React, { useState } from "react";
import { Grid, Paper, makeStyles } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import Rating from "@material-ui/lab/Rating";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import Button from "@material-ui/core/Button";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { Redirect } from "react-router";

const useStyle = makeStyles(theme => ({
  productCardItem: {
    maxWidth: 0
  },
  primaryPriceBlock: {
    fontWeight: "bold",
    fontSize: "1.2em",
    color: theme.palette.primary.main
  }
}));

export default function ProductCard(props) {
  const classes = useStyle();
  const [elevation, setElevation] = useState(2);
  const [redirectToDetailPage, setredirectToDetailPage] = useState(false);

  const onMouseEnter = function onMouseEnter() {
    console.log("enter");
    setElevation(elevation + 10);
  };
  const onMouseLeave = function onMouseLeave(params) {
    console.log("leave");
    setElevation(elevation - 10);
  };

  if (redirectToDetailPage) {
    return <Redirect push to="/detailPage" />;
  }

  return (
    <Paper
      onClick={event => {
        setredirectToDetailPage(true);
      }}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      elevation={elevation}
      style={{
        width: "22em",
        height: "100%",
        borderRadius: "0.5em"
      }}>
      <Grid
        container
        direction="column"
        alignItems="center"
        justify="center"
        spacing={2}
        style={{ padding: "1.5em 1.5em" }}>
        <Grid item md={8}>
          <img
            src={props.product.url}
            alt=""
            style={{ width: "auto", height: "20em" }}
          />
        </Grid>

        <Grid
          item
          style={{
            maxWidth: "100%",
            padding: "3em 0 1em 0"
          }}>
          <Typography
            variant="body2"
            style={{ fontWeight: "700", height: "2em" }}>
            {props.product.title}
          </Typography>
          <Typography variant="body2" style={{ textAlign: "right" }}>
            by {props.product.brand}
          </Typography>
        </Grid>
        <Grid container alignItems="center" justify="space-between">
          <Grid item>
            <Rating
              name="customized-empty"
              defaultValue={3.5}
              precision={0.5}
              emptyIcon={<StarBorderIcon fontSize="inherit" />}
              readOnly
            />
          </Grid>
          <Grid item>
            <Typography variant="body2" className={classes.primaryPriceBlock}>
              {props.product.price}&nbsp;
            </Typography>
          </Grid>
        </Grid>
        <Grid
          container
          alignItems="center"
          justify="space-between"
          style={{ width: "100%", paddingTop: "1em" }}>
          <Grid style={{ width: "49%" }} item>
            <Button
              variant="outlined"
              color="secondary"
              className={classes.button}
              endIcon={<FavoriteIcon color="error">send</FavoriteIcon>}
              style={{ width: "100%" }}>
              Wishlist
            </Button>
          </Grid>
          <Grid style={{ width: "49%" }} item>
            <Button
              variant="contained"
              color="primary"
              className={classes.button}
              startIcon={<ShoppingCartIcon>send</ShoppingCartIcon>}
              style={{ width: "100%" }}>
              Send
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
}
